/* Reset básico */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Estilos gerais */
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f9f9f9;
  color: #333;
  margin: 0;
  padding: 0;
  line-height: 1.6;
}


.emBreve {
  background: #2c333b;
  /* Cor de fundo verde */
  color: white;
  /* Cor do texto (opcional, para melhor contraste) */
  padding: 7px 20px;
  /* Espaçamento interno */
  border-radius: 10px;
  /* Bordas arredondadas */
  display: inline-block;
  /* Para que o fundo se ajuste ao conteúdo */
}

.emBreve:hover 
{
  background-color: #14c555;
}




/* Menu Bar */
header {
  background: #2c333b;
  color: #fff;
  padding: 1rem 2rem;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: 0 2px 7px rgba(0, 0, 0, 0.1);
}

nav {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0px;
}




.logoAgendou {
  width: 250px;
  height: 80px;
  object-fit: contain;
  /* Mantém a proporção dentro do espaço */
}


.nav-links {
  width: 100%;
  height: auto;
  list-style: none;
  display: flex;
  align-items: center;
  margin-bottom: 0px;

}


.nav-links li {
  width: 90%;
  height: auto;
  margin-left: 20px;
}

.nav-links a {
  width: 100%;
  height: auto;
  color: #fff;
  text-decoration: none;
  font-size: 17px;
  transition: color 0.3s;

}

.nav-links a:hover {
  color: #14c555;
}

.buttons {
  display: flex;
}



/* Menu Hambúrguer (Mobile) */
.menu-toggle {
  display: none;
  /* Esconde o ícone em telas maiores */
  cursor: pointer;
}

.menu-toggle img {
  width: 30px;
  /* Ajuste o tamanho da imagem conforme necessário */
  height: 30px;
  margin-top: 7px;
}


/* Estilo do bt login */

.btn-login {
  width: 100%;
  min-width: 170px;
  padding: 7px 20px;
  font-size: 16px;
  border: 2px solid #14c555;
  /* Contorno verde de 2px */
  border-radius: 10px;
  /* Arredondamento de 25px */
  cursor: pointer;
  background-color: transparent;
  /* Fundo transparente */
  color: #e3e9e6;
  /* Cor do texto verde */
  transition: background-color 0.3s ease, color 0.3s ease;
  flex: 1;
  /* Faz os botões terem o mesmo tamanho */
  text-align: center;
  /* Centraliza o texto dentro dos botões */
}


.btn-login:hover {
  background: #14c555;
  color: #ffffff;
}



/* Exibe o ícone em telas menores */
@media (max-width: 768px) {

  .btn-login {
    width: 100px;
    font-size: 16px;
  }


  .logoAgendou {
    width: 140px;
    height: 50px;
    object-fit: contain;
    /* Mantém a proporção dentro do espaço */
  }


  .menu-toggle {
    display: block;
    /* Mostra o ícone */
  }

  .nav-links {
    width: 100%;
    height: auto;
    display: none;
    /* Esconde o menu inicialmente */
    flex-direction: column;
    background: #2c333b;
    position: absolute;
    top: 60px;
    /* Ajuste conforme a altura do header */
    margin-top: 17px;
    left: 0;
    padding: 7px;
  }

  .nav-links li {
    margin-top: 13px;

  }


  .nav-links.active {
    display: flex;
    /* Mostra o menu quando ativo */
  }

  .buttons {
    display: none;
    /* Esconde os botões em mobile */
  }
}

/* Seção Hero */
.hero {
  background: url('../assets/images/heroEmpresas.png') no-repeat center center/cover;
  color: #fff;
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  /* Overlay escuro para melhorar a legibilidade */
}

.hero-content {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto; /* Centraliza o conteúdo */
}

/* Estilos para telas menores (tablets) */
@media (max-width: 768px) {
  .hero-content {
    padding: 15px;
  }
}

/* Estilos para telas muito pequenas (celulares) */
@media (max-width: 480px) {
  .hero-content {
    padding: 10px;
  }
}


.hero-content h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
  word-wrap: break-word;
  /* Compatibilidade com navegadores mais antigos */
  overflow-wrap: break-word;
  /* Padrão moderno para quebra de palavras */
  /* Preserva espaços em branco e quebras de linha */
  white-space: normal;
  /* Permite que o texto quebre naturalmente */
  /* Define o tamanho máximo do contêiner */
  max-width: 100%;
  /* Garante que o texto não ultrapasse a largura da tela */
  /* Adiciona um espaçamento interno (opcional) */
  padding: 7px;
}


.hero-content p {
  /* Quebra de palavras longas */
  word-wrap: break-word;
  /* Compatibilidade com navegadores mais antigos */
  overflow-wrap: break-word;
  /* Padrão moderno para quebra de palavras */

  /* Preserva espaços em branco e quebras de linha */
  white-space: normal;
  /* Permite que o texto quebre naturalmente */

  /* Define o tamanho máximo do contêiner */
  max-width: 100%;
  /* Garante que o texto não ultrapasse a largura da tela */

  /* Adiciona um espaçamento interno (opcional) */
  padding: 7px;

  /* Centraliza o texto (opcional) */
  text-align: center;

}


.hero-content .btn-cta {
  background: #14c555;
  color: #fff;
  padding: 0.8rem 2rem;
  text-decoration: none;
  border-radius: 10px;
  font-size: 1.1rem;
  transition: background 0.3s;
}


.hero-content .btn-cta:hover {
  background: #14c555;
}


/* Estilos para os botões de alternância */
.toggle-buttons {
  width: 100%;
  max-width: 700px;
  display: flex;
  justify-content: center;
  /* Centraliza horizontalmente */
  align-items: center;
  /* Centraliza verticalmente */
  margin-top: 20px;
  gap: 7px;
  /* Espaçamento entre os botões */
  border-radius: 10px;
  border: 2px;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 17px;
}

.toggle-buttons button {
  width: 100%;
  padding: 7px 20px;
  font-size: 16px;
  border: 2px solid #14c555;
  /* Contorno verde de 2px */
  border-radius: 10px;
  /* Arredondamento de 25px */
  cursor: pointer;
  background-color: transparent;
  /* Fundo transparente */
  color: #e3e9e6;
  /* Cor do texto verde */
  transition: background-color 0.3s ease, color 0.3s ease;
  flex: 1;
  /* Faz os botões terem o mesmo tamanho */
  text-align: center;
  /* Centraliza o texto dentro dos botões */
}


.toggle-buttons button.active {
  background-color: #000000;
  /* Fundo verde quando ativo */
  color: white;
  /* Texto branco quando ativo */
}

.toggle-buttons button:hover {
  background-color: #14c555;
  /* Fundo verde ao passar o mouse */
  color: white;
  /* Texto branco ao passar o mouse */
}




/* Seção de Funcionalidades */
.funcionalidades {
  padding: 4rem 2rem;
  text-align: center;
  background: #fff;
}

.funcionalidades h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #2c333b;
}

.funcionalidades .subtitle {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 4rem;
}

/* Container com layout responsivo */
.funcionalidades-container {
  display: grid;
  gap: 1.5rem;
  justify-content: center;
  padding: 0 1rem;
}

/* Layout padrão para telas menores (2 colunas) */
.funcionalidades-container {
  grid-template-columns: repeat(2, 1fr);
}

/* Layout para telas maiores (3 colunas) */
@media (min-width: 768px) {
  .funcionalidades-container {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* Seção de Funcionalidades */
.funcionalidades {
  padding: 4rem 2rem;
  text-align: center;
  background: #f9f9f9;
  /* Fundo mais claro */
}


/* Título e subtítulo */
.funcionalidades h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #2c333b;
}

.funcionalidades .subtitle {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 3rem;
}

/* Container Responsivo */
.funcionalidades-container {
  display: grid;
  gap: 1.5rem;
  justify-content: center;
  padding: 0 1rem;
}


/* Layout: 1 coluna (mobile), 2 colunas (tablet), 3 colunas (desktop) */
@media (max-width: 600px) {
  .funcionalidades-container {
    grid-template-columns: 1fr;
  }
}


@media (min-width: 601px) and (max-width: 1024px) {
  .funcionalidades-container {
    grid-template-columns: repeat(2, 1fr);
  }
}


@media (min-width: 1025px) {
  .funcionalidades-container {
    grid-template-columns: repeat(3, 1fr);
  }
}


/* Cartões de Funcionalidade */
.funcionalidade {
  background: rgba(255, 255, 255, 0.9);
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0px 7px 25px rgba(0, 0, 0, 0.1);
  text-align: left;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(7px);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}


/* Linha verde inferior */
.funcionalidade::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 6px;
  background: #14c555;
  border-radius: 0 0 7px 7px;
}


/* Efeito de flutuação ao passar o mouse */
.funcionalidade:hover {
  transform: translateY(-8px);
  box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.15);
  background: #d2ffd2;
}


/* Ícones */
.funcionalidade i {
  font-size: 2rem;
  color: #14c555;
  margin-bottom: 1rem;
}


/* Títulos */
.funcionalidade h3 {
  font-size: 1.3rem;
  margin-bottom: 0.8rem;
  color: #2c333b;
}


/* Descrição */
.funcionalidade p {
  font-size: 1rem;
  color: #333;
}


/* Rótulo "Em Breve" */
.funcionalidade .emBreve {
  font-size: 0.9rem;
  color: #ff5722;
  font-weight: bold;
  margin-bottom: 0.5rem;
}



/* Seção de Benefícios */
.benefits {
  padding: 4rem 2rem;
  text-align: center;
  background: #f9f9f9;
}

.benefits h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #2c333b;
}

.benefits .subtitle {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 4rem;
}

.benefits-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}

.benefit {
  background: #fff;
  padding: 2rem;
  border-radius: 25px;
  width: 300px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}

.benefit:hover {
  transform: translateY(-7px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  background: #d2ffd2;
}

.benefit .icon-container {
  width: 80px;
  height: 80px;
  background: #14c555;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 1.5rem;
}

.benefit .icon-container i {
  font-size: 2rem;
  color: #fff;
}

.benefit h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #2c333b;
}

.benefit p {
  font-size: 1rem;
  color: #333;
}




/* Seção de Planos */
.pricing {
  padding: 4rem 1rem;
  text-align: center;
  background: #f9f9f9;
}

.pricing h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #2c333b;
}

.pricing .subtitle {
  font-size: 1.1rem;
  color: #333;
  margin-bottom: 3rem;
}

.pricing-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

/* Estilo dos Planos */
.plan {
  background: #fff;
  border-radius: 15px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  padding: 2rem;
  width: 100%;
  max-width: 300px;
  transition: transform 0.3s, box-shadow 0.3s;
}

.plan:hover {
  transform: translateY(-7px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  background: #d2ffd2;
}

.plan.highlight {
  border: 2px solid #14c555;
}

.plan-header {
  margin-bottom: 1.5rem;
}

.plan-header h3 {
  font-size: 1.75rem;
  color: #2c333b;
  margin-bottom: 0.5rem;
}

.plan-header .price {
  font-size: 2.5rem;
  font-weight: bold;
  color: #14c555;
}

.plan-header .price span {
  font-size: 1rem;
  color: #666;
}

.plan-features {
  list-style: none;
  padding: 0;
  margin-bottom: 2rem;
  text-align: left;
}

.plan-features li {
  font-size: 0.95rem;
  color: #444;
  margin-bottom: 0.75rem;
  display: flex;
  align-items: center;
}

.plan-features li i {
  color: #14c555;
  margin-right: 0.5rem;
}


.btnloginplan {
  background: #000000;
  color: #fff;
  padding: 0.8rem 2rem;
  margin: 7px;
  text-decoration: none;
  border-radius: 7px;
  font-size: 1rem;
  transition: background 0.3s;
  display: inline-block;
  width: 100%;
  max-width: 600px;
  text-align: center;
}


.btnloginplan:hover {
  background: #14c555;
  border: 1px solid #000000;
}

/* Responsividade */
@media (max-width: 768px) {
  .pricing-container {
    flex-direction: column;
    align-items: center;
  }

  .plan {
    max-width: 100%;
  }
}



/* Responsividade */
@media (max-width: 768px) {
  .hero-content h1 {
    font-size: 2rem;
  }

  .hero-content p {
    font-size: 1rem;
  }

  .funcionalidade,
  .benefit,
  .plan {
    width: 100%;
  }
}


/* Estilos para os modais */


h2 {
  font-size: 2rem;
  margin-bottom: 1.5rem;
  color: #333;
  text-align: center;
}


/* Seção de Contato */
.contact {
  background-color: #fff;
  padding: 2rem;
  border-radius: 7px;
  box-shadow: 0 4px 7px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 2rem auto;
}

.contact form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contact input,
.contact textarea {
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  outline: none;
  transition: border-color 0.3s ease;
}

.contact input:focus,
.contact textarea:focus {
  border-color: #14c555;
}

.contact textarea {
  resize: vertical;
  min-height: 150px;
}

.contact button {
  background-color: #000000;
  color: #fff;
  padding: 0.75rem;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.contact button:hover {
  background-color: #14c555;
}


/* Estilo do formulário */
.input-container {
  position: relative;
  margin-bottom: 7px;
}





/* Seção FAQ */
.faq {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin: 0 auto;
}

/* Caixa de perguntas frequentes */
.faq-container {
  width: 100%;
  max-width: 80%;
  padding: 25px;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 0px 7px 30px rgba(0, 0, 0, 0.1);
  text-align: left;
  backdrop-filter: blur(7px);
}

/* Estilização dos itens da FAQ */
.faq-item {
  padding: 15px;
  border-left: 5px solid #14c555;
  background: #fff;
  border-radius: 7px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* Efeito de flutuação ao passar o mouse */
.faq-item:hover {
  transform: translateY(-5px);
  /* Move o item 5px para cima */
  box-shadow: 0px 15px 25px rgba(0, 0, 0, 0.15);
  /* Aumenta a sombra para efeito de elevação */
  background: #d2ffd2;
}

.faq-item h3 {
  margin-bottom: 5px;
  font-size: 1.2rem;
  font-weight: bold;
  color: #333;
}

.faq-item p {
  font-size: 1rem;
  color: #333;
}


/* Rodapé */
footer {
  background: #2c333b;
  color: #fff;
  text-align: center;
  padding: 1rem;
  margin-top: 2rem;
}



/* Rodapé */
.footer a {
  width: 100%;
  height: auto;
  color: #fff;
  text-decoration: none;
  font-size: 17px;
  transition: color 0.3s;
}


.politicas-link {
  color: #14c555;
  /* Cor azul */
  text-decoration: underline;
  /* Sublinhado */
  transition: color 0.3s ease;
  /* Transição suave de cor */
}

.politicas-link:hover {
  color: #6cf3d5;
  /* Cor azul mais escura ao passar o mouse */
}